<template>
  <div class="board">
    <div class="tile" style="width: 10%">
      <div>theProblem : 0</div>
      <div v-if="player == 0">
        <p>0</p>
      </div>
    </div>
    <div v-for="i in n" :key="i" class="tile" style="width: 10%">
      <div>theProblem : {{ i }}</div>
      <div v-if="player == i">
        <p>{{ i }}</p>
      </div>
    </div>
  </div>

  <div class="screen">
    <button class="dice" @click="randomStart"><p>전진하기</p></button>
  </div>
  <div class="black-bg" v-if="isModalOpen">
    <div class="white-bg">
      <h4>{{ problems[player-1]['question'] }}</h4>
      <button v-for="(a, i) in problems[player-1]['select']" :key="i" @click="answerSelect(i)">{{a}}</button>
    </div>
  </div>  
</template>

<script>

let problem = [
  {
    question: "재생에너지(Renewable Electricity) 100%의 약자로 기업이 사용하는 전력량의 100%를 2050년 까지 풍력, 태양광 등의 재생에너지로 충당하겠다는 목표로 탄소배출 저감을 위한 국제 캠페인은 RE100이다.",
    answer: "O",
    select: ["O", "X"]
  },
  {
    question: "지구온난화는 지구의 온도가 상승하는 현상이다.",
    answer: "O",
    select: ["O", "X"]
  },
  {
    question: "지구 평균 온도가 8℃ 상승하면 지구상 생물이 멸종한다고 예측한다.",
    answer: "X",
    select: ["O", "X"]
  },
  {
    question: "전체 온실가스 배출량의 77%를 차지하는 것은?",
    answer: "2. 이산화탄소(CO2)",
    select: ["1. 메탄(CH4)", "2. 이산화탄소(CO2)", "3. 아산화질소(N2O)"]
  },
  {
    question: "2015년 12월 파리협정에서는 산업혁명 이전 (1850년~1900년 평균) 대비 2℃ 보다 훨씬 아래로 유지하고, 나아가 ?℃ 아래로 억제하기 위해 노력해야 한다는 목표를 설정하였다. ?는 무엇일까?",
    answer: "1.5",
    select: ["1", "1.5", "2"]
  },
  {
    question: "지구온난화를 막기 위해 우리가 할 수 있는 노력이 아닌것은?",
    answer: "지구를 시원하게 하기위해 에어컨을 세게 계속 틀어둔다.",
    select: ["지구를 시원하게 하기위해 에어컨을 세게 계속 틀어둔다.", "가까운 곳은 걸어다닌다.", "일회용품 사용을 줄인다."]
  },
]
function shuffle(array) {
  array.sort(() => Math.random() - 0.5);
}
shuffle(problem)
export default {
  name: 'App',
  data() {
    return {
      isModalOpen: false,
      n: 6,
      currentState: 0,
      player: 0,
      problems: problem
    }
  },
  created() { 
    this.player = 0
  },
  methods: {
    randomStart() {
      this.player += 1; //Math.floor(Math.random() * 6) + 
      if (this.player >= this.n + 1) {
        alert("Thank you for playing");
        this.$router.push('/about');
      }
      else {
        this.isModalOpen = true
      }
    },
    answerSelect(answer) {
      if (this.problems[this.player - 1]["answer"] == this.problems[this.player - 1]["select"][answer]) {
        alert("정답")
        this.isModalOpen = false
      }
      else {
        alert("오답")
        this.player--
        if( this.player < 0) {
          this.player = 0
        }
        this.isModalOpen = false
      }
    }
  },
  components: {
  }
} 
</script>

<style>
.tile {
  background-color: aqua;
  border: solid black;
  border-radius: 25%;
  aspect-ratio: 1;
  float:left;
  margin: 1%;
}

.tile div {
  margin: 0;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.player {
  float:left;
}

body {
  margin: 0;
}
div {
  box-sizing: border-box;
}
.dice {
  width: 10%; aspect-ratio: 1;background: orange;
  border-radius: 8px;
  
}
.screen {
  width: 100%;
  position: fixed; /*padding-left: 1150px;*/
  padding-top: 500px;
}

.black-bg {
  width: 100%; height: 100%;
  background: rgba(0,0,0,0.5);
  position: fixed; padding: 20px;
}
.white-bg {
  width: 100%; background: white;
  border-radius: 8px;
  padding: 20px
}
</style>
